<template>
  <span class="blue_hover"
        style="font-size:40px;cursor:pointer; position:absolute; margin-right: 25px; margin-left: 25px;"
        v-on:click="openLog(true)">&#9776;</span>
  <div id="mySidenav" class="sidenav log_back scroll">
    <a class="close_btn blue_hover" v-on:click="openLog(false)">&times;</a>
    <div class="check_row row">
      <div class="row">
        <div class="error_letter" style="text-align: end">errors</div>
        <button style="background: none; border: none;" v-on:click="bErrors ^= true;">
          <div class="error_check" :class="bErrors? 'error_checked' : ''"></div>
        </button>
      </div>
      <div class="row">
        <div class="blue_letter" style="text-align: end">txs</div>
        <button style="background: none; border: none;" v-on:click="bTxs ^= true;">
          <div class="tx_check" :class="bTxs? 'tx_checked' : ''"></div>
        </button>
      </div>
    </div>
    <div class="list-group">
      <div v-for="index in logBuf.length" :key="index">
        <LogLine :msg="logBuf[index-1]" v-if="isChosen(logBuf[index-1].type)"/>
      </div>
    </div>
  </div>
  <div class="vl pale_blue_back" id="vl"></div>
</template>

<script>



import LogLine from "@/elements/LogLine";
import {useLocalStorage} from "@vueuse/core";
export default {
  components: {LogLine},
  name: "LogFlow",
  props: ['logBuf', 'openLog', 'isLogOpened'],
  watch: {
    isLogOpened: {
      handler(newVal){
        if(newVal){
          document.getElementById("mySidenav").style.width = "40%";
        }
        else{
          document.getElementById("mySidenav").style.width = "0%";
        }
      }
    }
  },
  mounted() {
    document.getElementById('vl').style.cssText = "min-height: "+(window.innerHeight-120).toString()+'px';
  },
  data(){
    return {
      bErrors: useLocalStorage("Log_bErrors", 0),
      bTxs: useLocalStorage("Log_bTxs", 0),
    }
  },
  methods: {
    isChosen(type){
      switch (type){
        case 0:
          return this.bTxs || !this.bErrors;
        case 1:
          return !(this.bErrors || this.bTxs);
        case 2:
          return this.bErrors || !this.bTxs;
        default: {
          return false;
        }
      }
    }
  }
}
</script>

<style scoped>
.error_check{
  margin-top: 5px;
  --h: 6px;
  height: calc(var(--h) * 2);
  width: calc(var(--h) * 2);
  border-radius: var(--h);
  background-color: #010207;
  border: 1px solid #525252;
}
.error_check:hover{
  background-color: #e88888;
  border: 1px solid #525252;
}
.error_checked{
  background-color: #d96868;
  border: 1px solid #d9d9d9;
}
.tx_check{
  margin-top: 5px;
  --h: 6px;
  height: calc(var(--h) * 2);
  width: calc(var(--h) * 2);
  border-radius: var(--h);
  background-color: #010207;
  border: 1px solid #525252;
}
.tx:hover{
  background-color: #98ecff;
  border: 1px solid #525252;
}
.tx_checked{
  background-color: #68bce0;
  border: 1px solid #d9d9d9;
}
.check_row{
  position: absolute;
  top: 5px;
  right: 75px;
  margin-left: auto;
}
.sidenav {
  height: 100vh;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
  text-align:center;
}
.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  display: block;
  transition: 0.3s;
}
.sidenav .close_btn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}
.vl {
  width: 2px;
  height: 100%;
  margin-top: 15px;
  margin-left: 75px;
  position: absolute;
  overflow: hidden;
}
.list-group{
  margin-bottom: 250px;
}
</style>